// ==========================================================================
// Form Elements
// ==========================================================================

.form {
	@extend %clearfix;
	margin: 0 auto;
	display: inline-block;
	@include e(label) {
		@include m(hidden) {
			@extend .srt;
		}
		display: block;
		margin-bottom: em(10px);
	}

	@include e(input) {
		font-size : em(18px);
		margin-bottom: em(20px, 18px);
		border-bottom: 6px solid palette(grey, x-light);
		border-radius: $br--default;
		background: palette(grey, xx-light);
		color: palette(black, dark);
		font-weight: $font-weight--light;

		&:focus {	
			border-color: $color-primary;
		}

	}

	@include e(btn) {
		@extend %btn;
		background: $color-primary;
	}
}

.searchbox {
	float: left;
	font-size: em(20px);
	border-top-left-radius: 7px;
	border-bottom-left-radius: 7px;
	height: 45px;
	width: 245px;
	padding: 10px 25px;
	@include mq(medium) {
		width: 355px;
	}
}

.searchbtn {
	float: left;
	background: $turquoise url('../img/magnifyingglass.svg') center center no-repeat;
	border: none;
	border-top-right-radius: 7px;
	border-bottom-right-radius: 7px;
	width: 50px;
	height: 45px;
}